.form {
    width: 500px;
}

.form__fieldset {
    border: 2px solid #ccc;
    border-radius: 10px;
    padding: 40px;
    margin: 20px 0;
    background-color: white;
    box-shadow: 0 0 30px black;
}

.form__legend {
    background-color: cadetblue;
    border-radius: 10px;
    padding: 10px;
    color: white;
}

.label--centered {
    display: flex;
    flex-direction: row;
    align-items: center;
}

@media(max-width: 620px) {
    .form {
        width: 90%;
    }
    .label--centered {
        flex-direction: column;
    }
}

.form__labelText {
    width: 100%;
    max-width: 200px;
    display: inline-block;
    margin-right: 5px;
}

.form__field {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 6px;
    width: 100%;
    max-width: 200px;
}

.form__button {
    background-color: cadetblue;
    color: white;
    border: none;
    border-radius: 10px;
    padding: 10px 60px;
    display: block;
    margin: 40px auto;
}

.form__button:hover {
    background-color: hsl(182, 25%, 55%);
}

.form__button:active {
    background-color: hsl(182, 25%, 62%);
}

.form__paragraph--centered {
    padding: 10px;
    text-align: center;
}

.form__paragraph--fontSmall {
    font-size: 13px;
}